<template>
  <div class="Analysis">
      <v-toolbar flat dense>
        <div style="height:25px;font-size:14px;"></div>
      </v-toolbar>
       <ve-scatter :data="chartData" :settings="chartSettings" :legend-visible="true"  :judge-width="true" theme-name="lottery" :mark-line="markLine" :mark-point="markPoint" :colors="colors" style="min-height:460px;"></ve-scatter>
  </div>
</template>

<script>
export default {
  props: ['lotteryMod','lotteryType','currentNum'],
  data() {
      this.markLine = {
        data: [
          {
            name: '',
            type: 'average'
          }
        ]
      }
      this.markPoint = {
        data: [
          {
            name: '',
            type: 'max'
          }
        ]
      }
      this.dataZoom = [
        {
          type: 'slider',
          start: 0,
          end: 100
        }
      ]
       this.colors = ['#d95850','#0098d9', '#e6b600',
        '#2b821d', '#005eaa','#339ca8', 
        '#cda819', '#32a487']

      this.chartSettings = {
        labelMap: {
          redBall: '',
          blueBall: ''
        }
      }
    return{
      chartData: {
        columns: ['num','redBall','blueBall'],
        rows:[],
      },
    }
  },
  mounted(){
    this.search()
  },
  watch:{
     lotteryType(){
      this.search()
    },

  },
  methods:{
     search(){
       this.$axios
        .get('/lottery/'+this.lotteryType+'/count')
        .then(response => {
          this.chartData.rows = response.data
        })
        .catch(e=> { // 
          window.console.log(e);
        });
     },

  }
}

</script>

<style scoped>
  .Analysis{
    height: 100%;
  }
  .homepage ul li span{
    width: 25px;
    height: 25px;
    border-radius: 25px;
  }
  .homepage ul li span.redball {
     background: red;
  }
  .homepage ul li span.blueball {
     background: blue;
  }
</style>
